

h1, h2, h3, h4, span, p, a {
    font-family: 'Poppins', sans-serif;
}

a {
    color: black;
    text-decoration: none;
}

a, i {
    transition: .3s all;
    cursor: pointer;
}

a:hover, i:hover {
    color: #f8b600;
}

.wrap {
    width: 1140px;
    margin: 0 auto;
}

.btn {
    padding: 10px 15px 10px 15px;
    background-color: #f8b600;
    border-radius: 3px;
    color: white;
}

// COLORS
// #f8b600 (jaune)
